<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-form :inline="true" style="margin-top: 10px" v-if="btn.add">
            <el-form-item style="float:right;">
                <el-button icon="el-icon-plus" type="primary" size="medium" plain @click="addRole"> </el-button>
            </el-form-item>
        </el-form>
        <!--table -->
        <el-table :data="roleLists.filter(data=>(!search || data.role_name.includes(search)))">
            <el-table-column label="#ID" prop='id' sortable align="center" width="100px"></el-table-column>
            <el-table-column label="" prop="role_name" align="center"></el-table-column>
            <el-table-column label="" v-if="btn.edit" align="center">
                <template slot-scope="scope">
                    <Radio :item="scope.row" :url="cgi.status"></Radio>
                </template>
            </el-table-column>
            <el-table-column label="" sortable prop="created_at" align="center"></el-table-column>
            <el-table-column label="" sortable prop="updated_at" align="center"></el-table-column>
            <el-table-column label="" align="right">
                <template slot="header" slot-scope="scope">
                    <el-input v-model="search" placeholder=""></el-input>
                </template>
                <template slot-scope="scope">
                    <el-button type="primary" plain icon="el-icon-edit" v-if="btn.edit" size="mini" @click="updateRole(scope.row)"> </el-button>
                    <Delete :url="cgi.remove" :item="scope.row" :index="scope.$index" :Lists="roleLists" v-on:success="success" v-if="btn.del"></Delete>
                </template>
            </el-table-column>
        </el-table>
        <!--table -->
        <!--table -->
        <div style="margin: 25px 0">
            <el-pagination
                @size-change="sizeChange"
                @current-change="currentChange"
                :page-sizes="[15, 30, 50, 100]"
                :page-size="limit"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
                :current-page="page">
            </el-pagination>
        </div>
        <!--table -->
        <!----->
        <el-dialog :title="title" :visible.sync="syncVisible" :width="dialogWidth" :center="true">
            <el-form :label-width="labelWidth" :model="roleModel" :ref="reFrom" label-position="left">
                <el-form-item label="" prop="role_name" :rules="[{ required:true,message:'',trigger:'blur' }]">
                    <el-input v-model="roleModel.role_name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" required prop="auth_ids">
                    <el-transfer :titles="['', '']" :button-texts="['', '']" v-model="defaultChecked" :data="authLists" filterable @change="handleChange"></el-transfer>
                </el-form-item>
                <el-form-item required label="" prop="status" v-if="reFrom === 'created'">
                    <Status :status="roleModel.status" @changeStatus="changeStatus"></Status>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <Submit :reFrom="reFrom" :model="roleModel" :url="url" :refs="refs" v-on:success="success"></Submit>
            </div>
        </el-dialog>
        <!----->
    </div>
</template>

<script>
    import apiLists from '../../api/api';
    import $url from '../../api/url';
    import func from '../../api/func'
    import Radio from "../common/Radio";
    import Delete from "../common/Delete";
    import Submit from "../common/Submit";
    import {mapGetters} from "vuex";
    import Status from "../common/Status";
    export default {
        name: "lists",
        components: {Status, Submit, Delete, Radio},
        data(){
            return {
                roleLists:[],
                loading:true,
                loadingText:'',
                authLists:[],
                defaultChecked:[],
                search:'',
                limit:15,
                page:1,
                total:0,
                title:'',
                syncVisible:false, //
                labelWidth:'100px',
                url:'',
                refs:this.$refs,
                reFrom:'created',
                act:'',
                roleModel:{},
                cgi:{insert:$url.roleSave, update:$url.roleUpdate, remove:$url.roleDelete, status:$url.roleUpdate},
                //
                btn:{},
            }
        },
        computed:{
            ...mapGetters(['userInfo','dialogWidth']),
        },
        methods:{
            /**
             * todo:
             * @param status
             */
            changeStatus:function (status) {
                this.roleModel.status = status
            },
            /**
             * todo
             * @param item
             */
            success:function(item){
                this.getRoleLists(this.page,this.limit)
            },
            /**
             * todo
             * @param page
             * @param limit
             */
            getRoleLists:function (page,limit) {
                let params = { page:page, limit:limit};
                this.syncVisible = false;
                apiLists.RoleLists(params).then(response=>{
                    if (response && response.data.code===200){
                        this.roleLists = response.data.item.role.data;
                        this.total = response.data.item.role.total;
                        this.authLists = response.data.item.auth;
                        this.loading = false;
                    }
                })
            },
            /**
             * todo
             * @param val
             */
            sizeChange:function(val){
                this.limit = val;
                this.getRoleLists(this.page,this.limit)
            },
            /**
             * todo
             * @param val
             */
            currentChange:function(val){
                this.page = val;
                this.getRoleLists(this.page,this.limit)
            },
            /**
             * todo
             */
            addRole:function () {
                this.title='';
                this.syncVisible = true;
                this.url = this.cgi.insert;
                this.reFrom = 'created';
                this.defaultChecked = [];
                this.roleModel={role_name:'', auth_ids:[], auth_url:[], status:1, created_at:func.get_timestamp(), updated_at:func.get_timestamp()}
            },
            /**
             * @param value      
             * @param direction  'left' / 'right'
             * @param movedKeys   key 
             */
            handleChange(value, direction, movedKeys) {
                switch (direction) {
                  //
                    case 'left':
                        movedKeys.map((item) => {
                            this.defaultChecked.splice(this.defaultChecked.indexOf(parseInt(item)),1);
                        });
                        break;
                  //
                    case 'right':
                        movedKeys.map((item) => {
                            this.defaultChecked.push(parseInt(item));
                        });
                        break;
                }
                this.roleModel.auth_ids = this.defaultChecked;
            },
            /**
             * todo
             * @param item
             */
            updateRole:function (item) {
                this.syncVisible = true;
                this.title = '';
                this.url = this.cgi.update;
                this.defaultChecked = []; //
                this.reFrom = 'update';
                JSON.parse(item.auth_ids).map(item=>{
                    this.defaultChecked.push(parseInt(item));
                })
                this.roleModel = item;
                this.roleModel.auth_ids = this.defaultChecked;
            },
        },
        mounted() {
            this.$nextTick(function () {
                this.btn = func.set_btn_status(this.$route.path,this.$route.name,this.userInfo.auth);
                this.getRoleLists(this.page,this.limit)
            });
        }
    }
</script>

<style scoped>

</style>



